.tg-news {
	&__articles-container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin: 0 auto;

		.article {
			flex: 2;
			min-width: 275px;
			border: 2px solid #EC0127;
			margin: 20px;
			display: flex;
			flex-direction: column;
			box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.35);

			.image {
				width: 100%;
				height: 130px;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: top center;

				@media screen and (max-width: 629px) {
					height: 180px;
				}
			}

			.info {
				flex: 3;

				p {
					font-weight: 300 !important;
					line-height: 20px;
				}

				.rd-more {
					width: 100%;

					a {
						color: #EC0127 !important;
					}
				}
			}
		}
	}

	&__pop-up {
		width: 100%;
		height: 100%;
		background: #fff;
		position: fixed;
		top: 0;
		z-index: 9999;
		padding: 50px 0;
		display: none;

		@media (max-width: 768px) {
			padding: 50px 15px;
		}

		&-container {
			overflow-y: auto;
			height: 100%;
		}

		.tg-close {
			cursor: pointer;
		}

		.image {
			width: 100%;
			height: 300px;
			background-size: cover;
			background-position: center 20%;
		}

		.body-of-article {
			max-width: 905px;
			margin: 0 auto;

			p {
				width: 100%;
				display: flex;
				min-width: 320px;
				font-size: 18px;
				font-weight: 300 !important;
				line-height: 20px;
				align-items: center;
				@media screen and (max-width: 768px) {
					flex-wrap: wrap;
					min-width: 100%;
				}

				img {
					width: 300px;
					max-height: 200px;
					margin: 0 30px;
					@media screen and (max-width: 768px) {
						margin: 15px auto;
					}
				}
			}
		}
	}
}